@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
  
}

body {
  background-color: #1f1d2b;
  font-family: 'Poppins', sans-serif;
  
}



.app {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 3rem;
  
  
}

.app .mainHeading h1 {
  text-align: center;
}

h1 {
  font-family: sans-serif;
}

.app h1,
h2 {
  color: white;
}

.app .input {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.app .input {
  margin-top: 3rem;
  width: 24em;
  height: 3em;
  background-color: #272636;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 6px;
  padding: 5px;
}

/* .app:hover .input {
  border-color: aliceblue;
} */

.app .input input {
  width: 100%;
  height: 1.6em;
  border-color: transparent;
  outline-color: transparent;
}

.app i {
  cursor: pointer;
  font-size: larger;
  font-weight: 900;
  color: grey;
  margin-right: 5px;
}
/* .app .todos {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
} */
.app .todos .todo {
  margin-top: 0.8em;
  width: 29em;
  min-height: 4rem;
  height: inherit;
  background-color: #272636;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 6px;
  padding: 5px;
  overflow: hidden;
  word-break: break-all;
  margin-bottom: 1rem;
}
.app .todos .todo .left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}

.app .todos .todo .left p {
  margin-left: 12px;
  font-size:  large;
  font-weight: 600;
  font-style: normal;
  color: #fff;
  margin-right: 10px;
}


.inputfield {
  font-size: large;
  background-color: #272636;
  outline: none;
  color: #fff;
  

}

.error-message {
  color: #fff;
  margin-top: 1em;
}

.todo-text {
  background-color: #272636;
  
}

::-webkit-input-placeholder {
    color: #fff;
}


.checkbox {
  margin-left: 1rem;
  zoom: 1.5;
  cursor: pointer;
  border: 1px solid #6f6fc8;
  border-radius: 3px;
  
  
  
}

.checkbox:checked {
    background-color: #6f6fc8;
    
}


.todo {
  border: 2px solid #272636;

  -webkit-transition: 0.5s;

  -o-transition: 0.5s;

  transition: 0.5s;
}

.todo:hover{
  border: 2px solid #6f6fc8;

}

.input {
  border: 2px solid #272636;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.input:hover {
  border: 2px solid #6f6fc8;
  /* background-color: #6f6fc8; */
}


.fa-trash-alt::before {
  margin-right: 10px;
  
}

.fa-plus::before {
  margin-right: 10px;
}

.todos {
  margin-top: 1rem;
  
}



@media only screen and (max-width: 600px) {            /*for mobile */

  .mainHeading {
    margin-left: 1rem;
  }

  .subHeading {
    margin-left: 1.5rem;
    font-size: small;
  }

  .app .input {
    width: 20em;
  }

  .app .todos .todo {
    width: 20em;
  }

}



@media screen and (device-aspect-ratio: 40/71) {         /* for iphone 5 */
  .app .input {
      width: 18em;
    }

    .app .todos .todo {
    width: 18em;
  }

  .app h1, h2 {
    color: white;
    font-size: 1.2em;
}




}



@media(max-width: 320px){                             /* for galaxy fold */
  .app .input {
    width: 15em;
  }

  .app .todos .todo {
    width: 15em;

  }

  .app h1, h2 {
    color: white;
    font-size: 1.2em;
}
  
}





